import { motion } from "framer-motion";

interface ViewFlowLogoProps {
  size?: "sm" | "md" | "lg";
  showText?: boolean;
  animated?: boolean;
}

export function ViewFlowLogo({ size = "md", showText = true, animated = true }: ViewFlowLogoProps) {
  const sizes = {
    sm: { icon: 28, text: "text-lg" },
    md: { icon: 36, text: "text-xl" },
    lg: { icon: 52, text: "text-3xl" },
  };

  const iconSize = sizes[size].icon;
  const textSize = sizes[size].text;

  return (
    <div className="flex items-center gap-3">
      {/* Logo Icon - Hexagonal Tech Design */}
      <motion.div
        whileHover={animated ? { scale: 1.1 } : {}}
        className="relative"
        style={{ width: iconSize, height: iconSize }}
      >
        <svg
          width={iconSize}
          height={iconSize}
          viewBox="0 0 52 52"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          {/* Outer Hexagon */}
          <motion.path
            d="M 26 4 L 44 14 L 44 34 L 26 44 L 8 34 L 8 14 Z"
            stroke="url(#hexGradient)"
            strokeWidth="2"
            fill="none"
            initial={{ pathLength: 0, opacity: 0 }}
            animate={animated ? { pathLength: 1, opacity: 1 } : { pathLength: 1, opacity: 1 }}
            transition={{ duration: 1.2, ease: "easeInOut" }}
          />

          {/* Inner Flow Lines - V shape */}
          <motion.path
            d="M 18 18 L 26 28 L 34 18"
            stroke="url(#flowGradient)"
            strokeWidth="2.5"
            strokeLinecap="round"
            strokeLinejoin="round"
            fill="none"
            initial={{ pathLength: 0, opacity: 0 }}
            animate={animated ? { pathLength: 1, opacity: 1 } : { pathLength: 1, opacity: 1 }}
            transition={{ duration: 0.8, delay: 0.4, ease: "easeOut" }}
          />

          {/* Flow continuation line */}
          <motion.path
            d="M 26 28 L 26 36"
            stroke="url(#flowGradient2)"
            strokeWidth="2.5"
            strokeLinecap="round"
            fill="none"
            initial={{ pathLength: 0, opacity: 0 }}
            animate={animated ? { pathLength: 1, opacity: 1 } : { pathLength: 1, opacity: 1 }}
            transition={{ duration: 0.6, delay: 0.8, ease: "easeOut" }}
          />

          {/* Energy Dots */}
          <motion.circle
            cx="26"
            cy="36"
            r="2.5"
            fill="url(#dotGradient)"
            initial={{ scale: 0, opacity: 0 }}
            animate={animated ? { scale: 1, opacity: 1 } : { scale: 1, opacity: 1 }}
            transition={{ duration: 0.4, delay: 1.2 }}
          />

          {/* Particle Flow Animation */}
          {animated && (
            <>
              <motion.circle
                cx="26"
                cy="24"
                r="1.5"
                fill="#00ccff"
                animate={{
                  cy: [18, 36],
                  opacity: [0, 1, 0],
                }}
                transition={{
                  duration: 2,
                  repeat: Infinity,
                  ease: "easeInOut",
                  delay: 0,
                }}
              />
              <motion.circle
                cx="22"
                cy="20"
                r="1"
                fill="#60a5fa"
                animate={{
                  cy: [16, 32],
                  opacity: [0, 0.8, 0],
                }}
                transition={{
                  duration: 2.5,
                  repeat: Infinity,
                  ease: "easeInOut",
                  delay: 0.5,
                }}
              />
              <motion.circle
                cx="30"
                cy="20"
                r="1"
                fill="#60a5fa"
                animate={{
                  cy: [16, 32],
                  opacity: [0, 0.8, 0],
                }}
                transition={{
                  duration: 2.5,
                  repeat: Infinity,
                  ease: "easeInOut",
                  delay: 0.7,
                }}
              />
            </>
          )}

          {/* Corner Accents */}
          <motion.line
            x1="42"
            y1="16"
            x2="38"
            y2="16"
            stroke="url(#accentGradient)"
            strokeWidth="1.5"
            strokeLinecap="round"
            initial={{ pathLength: 0, opacity: 0 }}
            animate={animated ? { pathLength: 1, opacity: 0.6 } : { pathLength: 1, opacity: 0.6 }}
            transition={{ duration: 0.5, delay: 1.4 }}
          />
          <motion.line
            x1="42"
            y1="32"
            x2="38"
            y2="32"
            stroke="url(#accentGradient)"
            strokeWidth="1.5"
            strokeLinecap="round"
            initial={{ pathLength: 0, opacity: 0 }}
            animate={animated ? { pathLength: 1, opacity: 0.6 } : { pathLength: 1, opacity: 0.6 }}
            transition={{ duration: 0.5, delay: 1.5 }}
          />

          {/* Gradients */}
          <defs>
            <linearGradient id="hexGradient" x1="0%" y1="0%" x2="100%" y2="100%">
              <stop offset="0%" stopColor="#00ccff" />
              <stop offset="50%" stopColor="#3b82f6" />
              <stop offset="100%" stopColor="#1e40af" />
            </linearGradient>
            <linearGradient id="flowGradient" x1="0%" y1="0%" x2="100%" y2="100%">
              <stop offset="0%" stopColor="#00ccff" />
              <stop offset="100%" stopColor="#60a5fa" />
            </linearGradient>
            <linearGradient id="flowGradient2" x1="0%" y1="0%" x2="0%" y2="100%">
              <stop offset="0%" stopColor="#00ccff" />
              <stop offset="100%" stopColor="#1e40af" />
            </linearGradient>
            <radialGradient id="dotGradient">
              <stop offset="0%" stopColor="#00ccff" />
              <stop offset="100%" stopColor="#0ea5e9" />
            </radialGradient>
            <linearGradient id="accentGradient" x1="0%" y1="0%" x2="100%" y2="0%">
              <stop offset="0%" stopColor="#00ccff" stopOpacity="0" />
              <stop offset="100%" stopColor="#00ccff" />
            </linearGradient>
          </defs>
        </svg>

        {/* Rotating Ring Glow */}
        {animated && (
          <motion.div
            className="absolute inset-0 rounded-full"
            style={{
              background: "radial-gradient(circle, rgba(0,204,255,0.3) 0%, transparent 70%)",
            }}
            animate={{
              opacity: [0.3, 0.6, 0.3],
              scale: [0.9, 1.1, 0.9],
            }}
            transition={{
              duration: 3,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        )}
      </motion.div>

      {/* Logo Text */}
      {showText && (
        <motion.span
          initial={animated ? { opacity: 0, x: -10 } : {}}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.5, delay: 0.5 }}
          className={`${textSize} font-bold bg-gradient-to-r from-cyan-400 via-blue-400 to-cyan-500 bg-clip-text text-transparent tracking-tight`}
        >
          ViewFlow Browser
        </motion.span>
      )}
    </div>
  );
}
